.home-grid {
  position: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 98%;
  text-align: center;
  justify-content: center; }

.banner-text h1 {
  font-size: 35px;
  color: white;
  font-family: "Work Sans";
  margin-top: 0em;
  margin-bottom: 0.25em; }

.banner-text p {
  font-size: 15px;
  color: white;
  font-family: "Open Sans"; }

.banner-text br {
  content: "";
  margin: -1em;
  display: block;
  font-size: 20%; }

.banner-text hr {
  border-top: 1px solid white;
  width: 15em;
  margin-top: 1.25em;
  margin-bottom: 1em;
  margin-left: auto;
  margin-right: auto; }

@media only screen and (max-width: 600px) {
  .banner-text h1 {
    margin-top: 5em; }
  .banner-text span {
    display: block;
    margin-top: -0.125em; } }

.social-links {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30em; }
  .social-links img {
    padding: 3px; }

.social-icon {
  height: 55px; }
