.projects-grid {
  position: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 98%;
  text-align: center;
  justify-content: center; }

.projects-desktop {
  display: flex;
  justify-content: center;
  align-items: center; }
  .projects-desktop a {
    text-decoration: none; }

.projects-mobile {
  justify-content: center;
  align-items: center;
  margin-bottom: -1.5em; }
  .projects-mobile a {
    text-decoration: none; }

.projects-header h1 {
  font-size: 35px;
  color: white;
  font-family: "Work Sans";
  margin-top: 30em; }

.projects-title {
  font-family: "Open Sans";
  font-weight: 700;
  color: black;
  font-size: 20px;
  margin: auto; }

.projects-desc {
  font-family: "Roboto Condensed";
  color: black;
  font-size: 17px;
  height: 120px;
  display: table-cell;
  vertical-align: middle;
  margin: auto;
  margin-top: -0.5em; }

.projects-tech {
  font-size: 13px;
  color: black;
  font-family: "Cousine";
  margin: auto; }

.projects-divider {
  content: "";
  display: block;
  margin-bottom: 40px; }

.projects-endpage {
  margin-bottom: 100em; }
  @media only screen and (min-width: 600px) {
    .projects-endpage br {
      content: "";
      display: block;
      margin-bottom: 15px; } }
